export const required = (value) => {
    //   ,  ,  ,  undefined
    if (value) {
        return undefined;
    }
    return '   ';
}

export const maxLengthCreator = (maxLength) => {
    return (value) => {
        if (value.length >= maxLength) {
            return `  : ${maxLength}`;
        }
        return undefined;
    }
}